'use strict';

/*
 * Parsing functions
 */
export const separateByLineBreak = (s) => s.split('\n');
export const separateByComma = (s) => s.split(', ');
export const separateBySpace = (s) => s.split(' ');
export const separateByPipe = (s) => s.split(' | ');


